@mixin bottomBorder {
  content: '';
  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translateX(-50%);
  width: 90%;
  height: 1px;
  background: $border-color; }

@mixin pseudo($width, $height) {
  content: '';
  position: absolute;
  width: $width;
  height: $height; }
